<template>
	<view class="search">
		<view class="search_top">
			<i class="iconfont icon-search" style="color: #333333;"></i>
			<input type="text" placeholder="搜索"  maxlength="12">
		</view>
		<view class="search_title">大家都在搜</view>
		<view class="search_keywords">
			<text v-for="(item, index) in arr" :key="index">{{item}}</text>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				arr: []
			};
		},
		onPullDownRefresh() {
			this.getLog();
		},
		created() {
			// let _this = this;
			// 调用云函数
			this.getLog();
		},
		methods: {
			getLog() {
				uni.showLoading({
					mask:true
				})
				uniCloud.callFunction({
					name: 'getIndexData',
					data: {
						myname: 'keywords'
					},
					success: res => {
						this.arr = res.result.data[0].arr;
						// console.log("1");
						// console.log(this);
						// console.log(_this);
					},
					complete:()=> {
						uni.hideLoading();
						uni.stopPullDownRefresh();
					}
				});
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import url("../../styles/iconfonts1.css");

	.search {
		padding: 20rpx;

		.search_top {
			background: #efefef;
			height: 60rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-radius: 10rpx;
			overflow: hidden;

			.iconfont {
				height: 80rpx;
				line-height: 80rpx;
				margin-top: 5rpx;
				width: 60rpx;
				display: block;
				// display:flex;

				text-align: center;
				color: #999;
			}

			input {
				flex: 1;
				height: 60rpx;
			}
		}

		.search_title {
			height: 120rpx;
			line-height: 120rpx;
			border-bottom: 1rpx solid #ccc;
		}

		.search_keywords {
			padding-top: 20rpx;

			text {
				float: left;
				width: 33.33%;
				line-height: 60rpx;
				font-size: 30rpx;
			}
		}
	}
</style>
